@mixin bgimg-cc-c($img){
    background-image: url($img);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
}

.page__classic__header{
    position: fixed;
    top: 50px;
    left: 0;
    right: 0;
    z-index: 1;
    height: 35px;
    background-color: $color-white;
    border-bottom: 1px solid #DCDCDC;
    @media($tablet){
        top: 108px;
        height: 69px;
    }
    @media($desktop){
        height: 103px;
    }
    .list{
        height: 100%;
        display: flex;
        @media($desktop){
            max-width: 1200px;
            margin: 0 auto;}
    }
    .item{
        flex-grow: 1;
        height: 100%;
        background-color: $color-white;
        border: 1px solid #DCDCDC;
        position: relative;
        transition: .4s;
        &:hover{
        border-bottom: 4px solid $color-secondary;     
            @media($tablet){
                border-bottom: 8px solid $color-secondary;
            }
            @media($desktop){
                border-bottom: 16px solid $color-secondary;
            }
        }
    }
    .selected{
        border-bottom: 4px solid $color-secondary;      
        @media($tablet){
            border-bottom: 8px solid $color-secondary;
        }     
        @media($desktop){
            border-bottom: 16px solid $color-secondary;
        } 
    }
    a{
        font-size: 14px;
        line-height: 35px;
        font-weight: 500;
        color: $color-black;
        text-align: center;
        display: block;
        @media($tablet){
        font-size: 28px;
        line-height: 69px;
        }
        @media($desktop){
        font-size: 42px;
        line-height: 103px;
        }
    }
    img{
        max-width: 15px;
        max-height: 15px;
        margin-right: 5px;
        vertical-align: middle;
        filter: brightness(0%) grayscale(100%);
        @media($tablet){
            max-width: 32px;
            max-height: 32px;
            margin-right: 8px;
        }
        @media($desktop){
            max-width: 48px;
            max-height: 48px;   
        }
    }
}

.page__classic__banner{
    margin-top: 85px;
    display: flex;
    height: 144px;
    @media($tablet){
        margin-top: 177px;
        height: 309px;
    }    
    @media($desktop){
        margin-top: 211px;               
    }
    .img1{
        flex-grow: 1;
        @media($desktop){
            margin-right: 24px;       
        }
    }
    .img2{
        flex-grow: 1;
    }

    .optical1{
        @include bgimg-cc-c('../images/2.png');
    }
    .optical2{
        @include bgimg-cc-c('../images/11.png');
    }
}

.page__classic__title{
    font-size: 20px;
    line-height: 26px;
    font-weight: 500;
    color: $color-black;
    padding-top: 24px;
    text-align: center;
    @media($tablet){
        font-size: 32px;
        line-height: 43px;
        padding-top: 40px;
    } 
    @media($desktop){
        font-size: 42px;
        line-height: 55px;
        padding-top: 88px;
    }
}

.page__classic__subtitle{
    font-size: 16px;
    line-height: 22px;
    font-weight: 300;
    color: $color-black;
    margin-bottom: 20px;
    text-align: center;
    @media($tablet){
        font-size: 24px;
        line-height: 33px;
        margin-bottom: 32px;
    } 
    @media($desktop){
        font-size: 32px;
        line-height: 45px;
        margin-bottom: 48px;
    }  
}

.page__classic__list{
    height: (203+32)*3px;
    margin-bottom: 8px;
    overflow: hidden;
    @media($tablet){
        height: (220+72)*5px;
        margin-bottom: -19px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    } 
    @media($desktop){
        height: (224+104)*3px;
        margin-bottom: -16px;
    }

    .item{
        position: relative;
        margin-bottom: 32px;
        @media($tablet){
            margin-bottom: 72px;
            width: 324px;
        } 
        @media($desktop){
            margin-bottom: 104px;
            width: 282px;
        }
    }
    .img{
        display: block;
        width: 100%;
        height: 166px;
        @media($tablet){
            height: 164px;
        }
    }
    .title{
        text-transform: uppercase;
        text-align: left;
        font-size: 16px;
        line-height: 21px;
        font-weight: 400;
        color: $color-black;
        @media($tablet){
            font-size: 24px;
            line-height: 32px;
        } 
    }
    .option__list{
        display: flex;
        &__item{
            width: 15px;
            height: 16px;
            border-radius: 4px;
            margin-right: 5px;
            @media($tablet){
                width: 24px;
                height: 24px;
                margin-right: 8px;
            }
            &:nth-of-type(1){background-color: #5F3E2D;}
            &:nth-of-type(2){background-color: #B75929;}
        }
    }
    .price{
        text-transform: uppercase;
        font-size: 16px;
        line-height: 21px;
        font-weight: 500;
        color: $color-secondary;
        position: absolute;
        top: 166px;
        right: 0;
        @media($tablet){
            font-size: 24px;
            line-height: 32px;
            top: 164px;
        } 
    }
}

.page__classic__pagelist{
    display: flex;
    justify-content: center;
    margin-bottom: 56px;
    @media($tablet){
        margin-bottom: 104px;
    } 
    @media($desktop){
        margin-bottom: 156px;   
    }

    .item{
        height: 32px;
        border: 1px solid #555555;
        background-color: $color-white;
        &:last-child{background-color: #F2F2F2;}
        @media($tablet){
        height: 48px;
        }
    }
    .selected{
        background-color: $color-black;
        a{color: $color-white;}
    }
    a{
        text-align: center;
        font-size: 12px;
        line-height: 32px;
        font-weight: 400;
        color: #555555;
        padding: 8px;
        @media($tablet){
            font-size: 19px;
            line-height: 48px;
        }
    }
}